<template>
    <div class="fbm_manual">
        <!-- <h4>手动发货</h4> -->
        <div class="select_opt">
            <el-form :model="fbm_formData" label-width="80px">
                <el-form-item label="选择仓库">
                    <el-select
                        @change="changeWarehouse"
                        v-model="fbm_formData.warehouse"
                        placeholder="请选择仓库"
                    >
                        <el-option
                            v-for="(item, i) in warehouses"
                            :key="i"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                    <!-- <el-input v-model="yc" readonly="readonly"></el-input> -->
                </el-form-item>
            </el-form>
        </div>
        <div class="card_com">
            <component :is="comName" :type="'com'"></component>
        </div>
    </div>
</template>

<script>
import YunCangCom from "@/components/orderManagement/FBM/YunCangCom";
import EasyWarehouseCom from "@/components/orderManagement/FBM/EasyWarehouseCom";
import ShunFengCom from "@/components/orderManagement/FBM/ShunFengCom";
export default {
    data() {
        return {
            warehouses: [
                {
                    label: "美国",
                    value: "mfyc",
                },
                {
                    label: "欧洲",
                    value: "yc",
                },
                // {
                //   label: "顺丰",
                //   value: "sf"
                // }
            ],
            // yc:'易仓',
            fbm_formData: {
                warehouse: "yc",
            },
            comName: "EasyWarehouseCom",
        };
    },
    components: {
        EasyWarehouseCom,
        YunCangCom,
        ShunFengCom,
    },
    methods: {
        changeWarehouse(val) {
            console.log(val);
            this.comName =
                val === "mfyc"
                    ? "YunCangCom"
                    : val === "yc"
                    ? "EasyWarehouseCom"
                    : "ShunFengCom";
        },
    },
};
</script>

<style scoped>
.card_com {
    /* border: 1px solid #e5e5e5; */
    padding: 30px 0;
    /* background: rgba(255, 255, 255, 0.9); */
}
.fbm_manual {
    padding-top: 22px;
}
</style>
<style>
.fbm_manual .select_opt .el-form {
    width: 297px;
    background: #fff;
    /* padding: 8px; */
    margin-left: 30px;
    border-radius: 4px;
}
</style>



