<template>
    <div class="yuncang">
        <el-form
            :model="ship_one_form"
            :rules="rules"
            ref="ruleForm"
            label-width="200px"
        >
            <div class="div_column">
                <el-form-item
                    prop="order_number"
                    label="请输入亚马逊订单编号"
                    required
                >
                    <el-input
                        v-model="ship_one_form.order_id"
                        placeholder="请输入亚马逊订单编号"
                    ></el-input>
                </el-form-item>
                <!-- <el-form-item label="请选择国家">
                    <el-select
                        v-model="ship_one_form.country"
                        placeholder="请选择国家"
                    >
                        <el-option
                            v-for="(item, i) in countries"
                            :key="i"
                            :label="item"
                            :value="item"
                        ></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="请填写邮编" required prop="postcode">
                    <el-input
                        v-model="ship_one_form.zip_code"
                        placeholder="请填写邮编"
                    ></el-input>
                </el-form-item>
            </div>

            <div class="div_column">
                <el-form-item
                    prop="receive_name"
                    label="请输入收货人姓名"
                    required
                >
                    <el-input
                        v-model="ship_one_form.name"
                        placeholder="请输入收货人姓名"
                    ></el-input>
                </el-form-item>
                <el-form-item label="请输入电话" required prop="tel">
                    <el-input
                        v-model="ship_one_form.tel"
                        placeholder="请输入电话"
                    ></el-input>
                </el-form-item>
            </div>

            <div class="div_column">
                <el-form-item label="请填写城市" prop="city" required>
                    <el-input
                        v-model="ship_one_form.city"
                        placeholder="请填写城市"
                    ></el-input>
                </el-form-item>
                <el-form-item
                    label="请填写州/省编码"
                    prop="province_code"
                    required
                >
                    <el-input
                        v-model="ship_one_form.province_code"
                        placeholder="请填写州/省编码"
                    ></el-input>
                </el-form-item>
            </div>

            <div class="div_column">
                <el-form-item
                    prop="address"
                    label="请填写详细地址"
                    required
                    style="width: 90%"
                >
                    <el-input
                        v-model="ship_one_form.address"
                        placeholder="请填写详细地址"
                    ></el-input>
                </el-form-item>
            </div>

            <div class="div_column">
                <el-form-item label="站点" required prop="station">
                    <el-input
                        v-model="ship_one_form.station"
                        placeholder="请填写站点"
                    ></el-input>
                </el-form-item>
                <el-form-item label="发货商品sku" required prop="sku">
                    <el-input
                        v-model="ship_one_form.sku"
                        placeholder="请填写发货商品sku"
                    ></el-input>
                </el-form-item>
            </div>

            <div class="div_column">
                <el-form-item label="订单配送方式" required prop="delivery">
                    <el-input
                        v-model="ship_one_form.delivery"
                        placeholder="请填写订单配送方式"
                    ></el-input>
                </el-form-item>
                <el-form-item label="订单详情id" required prop="details">
                    <el-input
                        v-model="ship_one_form.details"
                        placeholder="请填写订单详情id"
                    ></el-input>
                </el-form-item>
            </div>

            <div class="div_column">
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('ruleForm')"
                        >提交</el-button
                    >
                </el-form-item>
            </div>
        </el-form>
        <!-- <el-form class="uploadFile">
            <el-form-item label="魔方云仓批量发货">
                <el-upload
                    class="upload-demo"
                    ref="upload"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :file-list="fileList"
                    :auto-upload="false"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                >
                    <el-button slot="trigger" size="small" type="primary"
                        >选取文件</el-button
                    >
                    <el-button
                        style="margin-left: 10px"
                        size="small"
                        type="primary"
                        @click="submitUpload"
                        >提交</el-button
                    >
                </el-upload>
            </el-form-item>
        </el-form> -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            fileList: [],
            countries: ["美国"],
            ship_one_form: {
                order_id: "",
                // country_code: "",
                province_code: "",
                city: "",
                address: "",
                name: "",
                tel: "",
                zip_code: "",
                sku: "",
                station: "",
                delivery: "",
                details: "",
            },
            rules: {
                order_number: [
                    {
                        required: true,
                        message: "请输入亚马逊订单编号",
                        trigger: "blur",
                    },
                ],
                receive_name: [
                    {
                        required: true,
                        message: "请输入收货人姓名",
                        trigger: "blur",
                    },
                ],
                tel: [
                    { required: true, message: "请输入电话", trigger: "blur" },
                ],
                city: [
                    { required: true, message: "请填写城市", trigger: "blur" },
                ],
                province_code: [
                    {
                        required: true,
                        message: "请填写州/省编码",
                        trigger: "blur",
                    },
                ],
                address: [
                    {
                        required: true,
                        message: "请填写详细地址",
                        trigger: "blur",
                    },
                ],
                postcode: [
                    { required: true, message: "请填写邮编", trigger: "blur" },
                ],
                // sku: [
                //     {
                //         required: true,
                //         message: "请填写发货商品sku",
                //         trigger: "blur",
                //     },
                // ],
            },
        };
    },
    methods: {
        onSubmit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert("submit!");
                    let url = "server/order_management/usa_order_deliver/";
                    let formData = new FormData();
                    formData.append("order_id", this.ship_one_form.order_id);
                    formData.append(
                        "province_code",
                        this.ship_one_form.province_code
                    );
                    formData.append("city", this.ship_one_form.city);
                    formData.append("address", this.ship_one_form.address);
                    formData.append("name", this.ship_one_form.name);
                    formData.append("tel", this.ship_one_form.tel);
                    formData.append("zip_code", this.ship_one_form.zip_code);
                    formData.append("zip_code", this.ship_one_form.sku);
                    formData.append("zip_code", this.ship_one_form.station);
                    formData.append("zip_code", this.ship_one_form.delivery);
                    formData.append("zip_code", this.ship_one_form.details);
                    console.log(this.ship_one_form);
                    this.$axios.post(this._url2 + url, formData).then((res) => {
                        console.log("编辑数据提交", res.data);
                        if (res.data.code === 200) {
                            alert("提交成功");
                            window.location.reload();
                        } else {
                            alert(res.data.msg);
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
    },
};
</script>

<style scoped>
.yuncang .el-form {
    border: 1px solid #e5e5e5;
    padding: 40px 50px 20px 0px;
    border-radius: 6px;
    margin-bottom: 20px;
}
.yuncang .easywarehouse.el-form {
    text-align: center;
    border: 1px solid #e5e5e5;
    border-radius: 6px;
    padding-top: 20px;
}
.yuncang .easywarehouse.el-form > .el-form-item {
    width: 250px;
    margin: 0px auto;
}
.yuncang .el-form-item__content {
    text-align: center;
}
.yuncang .el-form-item__content > .el-button.el-button--primary {
    margin-left: -200px;
}
.card_com .yuncang .el-select {
    width: 100%;
}
.yuncang .uploadFile .el-form-item {
    width: 350px;
    margin: 0 auto;
    margin-top: -16px;
}
.div_column {
    display: flex;
    justify-content: space-around;
    width: 60%;
    margin: auto;
}
</style>
<style scoped>
/* .yuncang {
  width: 50%;
  margin: 20px auto;
} */
</style>



